import React from "react";
import { Link } from "react-router-dom";
import { Container, Banner, BannerContent, Content } from "./styles";
import post1 from "../../assets/images/post1.png";
import post2 from "../../assets/images/post2.png";
import post3 from "../../assets/images/post3.png";

const Home: React.FC = () => {
  return (
    <Container>
      <Banner>
        <BannerContent>
          <span>"</span>
          <h1>
            Olá, <input type="text" placeholder="Digite seu nome"/>
          </h1>
          <p className="phrase">
            O que te trouxe até aqui, não será o que te levará para o próximo nível.
          </p>
          <p className="author">
            - Autor Desconhecido
            <span>"</span>
          </p>
        </BannerContent>
      </Banner>
      <Content>
        <div className="thumbnail">
          <div className="img-thumbnail">
            <img src={post1} alt=""/>
          </div>
          <p>
            <Link to="/posts/post1">
              - Livros que mudaram minha carreira
            </Link>
          </p>
        </div>
        <div className="thumbnail">
          <div className="img-thumbnail">
            <img src={post2} alt=""/>
          </div>
          <p>
            <Link to="/posts/post2">
              - Quando eu me tornei um programador melhor
            </Link>
          </p>
        </div>
        <div className="thumbnail">
          <div className="img-thumbnail">
            <img src={post3} alt=""/>
          </div>
          <p>
            <Link to="/posts/post3">
              - Aprendendo a análisar um problema
            </Link>
          </p>
        </div>
      </Content>
    </Container>
  );
};

export default Home;