import styled from "styled-components";

export const Container = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
  background-color: #1C1B1B;
  border-bottom: 5px #918965 solid;
  padding: 0 10%;
`;

export const Brand = styled.div`
  width: 20%;
  
  img {
    width: 100%;
  }
`;

export const Menu = styled.div`
  display: flex;
  
  a {
    color: #918965;
    text-decoration: none;
    padding: 20px;
    font-size: 20px;
    
    &:hover {
      border-top: 2px solid #918965;
    }
    
    &:active {
      border-top: 2px solid #918965;
    }
  }
`;