  import styled from "styled-components";
  import bannerImg from "../../assets/images/banner.png";

  export const Container = styled.div`
    height: 100vh;
    width: 100%;
    background-color: #1C1B1B;
  `;

  export const Banner = styled.div`
    width: 100%;
    height: 700px;
    background-image: url(${bannerImg});
    background-size: cover;
    background-attachment: fixed;
    display: flex;
    justify-content: left;
    align-items: center;
    border-bottom: 5px #918965 solid;
    
    h1 {
      font-size: 72px;
      color: #918965;
      font-weight: normal;
      display: inline-block;
    }
    
    span {
      font-family: 'IM Fell Great Primer SC', serif;
      font-size: 72px;
      color: #918965;
    }
    
    p.phrase {
      width: 460px;
      text-align: center;
      font-size: 20px;
      color: #AFAFAF;
    }
    
    p.author {
      text-align: right;
      font-size: 18px;
      color: #AFAFAF;
    }
    
    input {
      border: none;
      background: transparent;
      border-bottom: 2px solid #918965;
      width: 260px;
      font-size: 40px;
      color: #918965;
      
      ::placeholder {
        font-size: 18px;
      }
      
      :focus {
        outline: none;
      }
    }
  `;

  export const BannerContent = styled.div`
     margin-left: 200px;  
  `;

  export const Content = styled.div`
    margin: 20px 200px;
    display: flex;
    justify-content: space-between;
    
    div.thumbnail {
      height: 400px;
      
      p {
        margin-top: 20px;
        text-align: center;
        width: 200px;
        
        a {
          color: #AFAFAF;
          text-decoration: none;
        }
      }
      
      div.img-thumbnail {
        border: 1px black solid;
        background: #918965;
        border-radius: 100%;
        height: 200px;
        width: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0 5px 10px #080808;
      }
    }
    
  `;
